import React, {
    useState,
    useCallback,
    useEffect
  } from 'react';
import queryString from 'query-string';
import {
  Box,
  Container,
  Paper,
  Typography,
  makeStyles
} from '@material-ui/core';
import Page from 'src/components/Page';
import {getContentApi}  from '../../api/contents';
import useIsMountedRef from 'src/hooks/useIsMountedRef';
const useStyles = makeStyles((theme) => ({
  root: {
    backgroundColor: theme.palette.background.dark,
    minHeight: '100%',
    paddingTop: theme.spacing(3),
    paddingBottom: theme.spacing(3)
  },
  paper:{
      padding:30,
      paddingTop:60
  }
}));

const NewsDetailView = (props) => {
  const {content_id } = queryString.parse(props.location.search);
  console.log(content_id);
  const classes = useStyles();
  const isMountedRef = useIsMountedRef();
  const [content, setContent] = useState(null);

  const getContent = useCallback(async () => {
    try {
       let content = await getContentApi(content_id);
      if (isMountedRef.current) {
        setContent(content);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getContent();
  }, [getContent]);

  if (!content) {
    return null;
  }
  return (
    <Page
      className={classes.root}
      title="HBON News"
    >
        
      <Container maxWidth="lg">
      <Paper className={classes.paper}>
        <Typography
          variant="h3"
          color="textPrimary"
        >
          {content.title}
        </Typography>
        <Box mt={3}>
          
          <Typography
                key={content.id}
                component="div"
                dangerouslySetInnerHTML={{ __html: content.detail }}
            />

          
        </Box>
        </Paper>
      </Container>
      
    </Page>
  );
};

export default NewsDetailView;
